<template>
  <div>
    <a-row type="flex" justify="center" class="cc-header">
      <a-col :xxl="20" :md="22" :span="24">
        <div class="cc-main title">
          <div>Colon Cancer</div>
          <div>Dynamic Survival Calculator</div>
        </div>
        <img class="himg" src="./assets/images/himg.png" />
      </a-col>
    </a-row>
    <a-row class="cc-body" type="flex" justify="center">
      <a-col :xxl="20" :md="22" :span="24">
        <div class="cc-main">
          <a-row v-if="load" :gutter="16">
            <a-col :md="12" :span="24" style="min-width:400px">
              <a-form layout="vertical" class="cc-form">
                <a-form-item>
                  <slot name="label">
                    <div class="form-lbl">
                      <div class="rect"></div>
                      Survival time since diagnosis
                    </div>
                  </slot>
                  <a-select v-model="survprob">
                    <a-select-option
                      v-for="month in months"
                      :key="month"
                      :value="month"
                    >
                      {{ month }} months
                    </a-select-option>
                  </a-select>
                </a-form-item>
                <a-form-item
                  v-for="(item, index) in point_scores"
                  :key="item.type"
                >
                  <slot name="label">
                    <div class="form-lbl">
                      <div class="rect"></div>
                      {{ item.type }}
                    </div>
                  </slot>
                  <a-radio-group v-model="scores[index]">
                    <a-radio-button
                      v-for="point in item.points"
                      :key="point.label"
                      :value="point.value"
                    >
                      {{ point.label }}
                    </a-radio-button>
                  </a-radio-group>
                </a-form-item>
              </a-form>
            </a-col>
            <a-col :md="12" :span="24" style="min-width:400px">
              <div class="cc-result">
                <div class="dline"></div>
                <div class="info-area">
                  <div class="info">
                    <div class="circ">1</div>
                    -year survival probability: <span class="res">{{ showProb(1) }}</span>
                  </div>
                  <div class="info">
                    <div class="circ">3</div>
                    -year survival probability: <span class="res">{{ showProb(3) }}</span>
                  </div>
                  <div class="info">
                    <div class="circ">5</div>
                    -year survival probability: <span class="res">{{ showProb(5) }}</span>
                  </div>
                </div>
                <div class="chart-area">
                  <div class="circ">
                    <a-icon class="label" type="line-chart" />
                  </div>
                  <chart :value="probs"></chart>
                </div>
              </div>
            </a-col>
          </a-row>
          <a-row v-else>
            <a-col>
              <a-result title="Page loading, please wait a moment!">
                <template #icon>
                  <a-icon
                    type="sync"
                    spin
                    :style="{ color: theme.themeColor }"
                  />
                </template>
              </a-result>
            </a-col>
          </a-row>
        </div>
      </a-col>
    </a-row>
  </div>
</template>

<script>
import chart from "./components/chart";
export default {
  components: { chart },
  data() {
    return {
      theme: this.$theme.style,
      point_scores: [],
      scores: [],
      survprob: 0,
      cals: [[], [], [], [], []],
      load: false,
      limits: [0, 93, 81, 69, 57, 45],
      mychart: null,
    };
  },
  created: function () {
    var inits = [this.initScores()];
    for (var i = 1; i <= 5; i++) {
      inits.push(this.initCal(i));
    }
    var that = this;
    Promise.all(inits).then(function () {
      var probs = [];
      for (var i = 1; i <= 5; i++) {
        probs.push(that.caculate(i));
      }
      that.probs = probs;
      that.load = true;
    });
  },
  watch: {
    scores: function () {
      if (!this.load) return;
      this.updateProbs();
    },
    survprob: function () {
      if (!this.load) return;
      this.updateProbs();
    },
  },
  methods: {
    initScores: function () {
      var that = this;
      return new Promise(function (resolve, reject) {
        that.$axios.get("./static/data/pointscore.json").then(function (res) {
          that.point_scores = res.data;
          for (var i = 0; i < that.point_scores.length; i++) {
            var p = that.point_scores[i];
            that.scores.push(p.points[0].value);
          }
          resolve();
        });
      });
    },
    initCal: function (y) {
      var that = this;
      return new Promise(function (resolve, reject) {
        that.$axios
          .get("./static/data/" + y + "ycalculator.json")
          .then((res) => {
            that.cals[y] = res.data;
            resolve();
          });
      });
    },
    showProb: function (y) {
      var prob = this.probs[y - 1];
      return prob == -1 ? "" : prob + "%";
    },
    updateProbs: function () {
      var probs = [];
      for (var i = 1; i <= 5; i++) {
        probs.push(this.caculate(i));
      }
      this.probs = probs;
    },
    caculate: function (y) {
      if (this.survprob > this.limits[y]) {
        return -1;
      }
      var sum = 0;
      for (var i = 0; i < this.scores.length; i++) {
        sum += this.scores[i];
      }
      var survprob = "survprob" + this.survprob;
      var k = 0;
      for (; k < this.cals[y].length; k++) {
        if (this.cals[y][k].points >= sum) break;
      }
      if (
        k > 0 &&
        sum - this.cals[y][k - 1].points < this.cals[y][k].points - sum
      ) {
        k -= 1;
      }
      var prob = this.cals[y][k][survprob];
      prob *= 100;
      prob = Math.round(prob * 100) / 100;
      return prob;
    },
  },
  computed: {
    months: function () {
      var lst = [];
      for (var i = 0; i <= 93; i += 3) {
        lst.push(i);
      }
      return lst;
    },
  },
};
</script>
<style scoped>
.cc-main {
  width: 90%;
  margin: 0 auto;
}
</style>


